import helper from '../../../common/helper.js';
export default {
	data() {
		return {
			searchText: '',
			history: null,
			popular: null
		}
	},
	onLoad: function() {
		this.getHistory();
		this.refreshPopular();
	},
	methods:{
			goSearchByHisAndPop: function(event) {
				let that=this;
				that.searchText = event.currentTarget.dataset.text;
				uni.navigateTo({
					url: '../../searchItemDetail/searchItemDetail?keywords='+that.searchText
				})
			},
			refreshPopular: function() {
				let that = this;
				uni.request({
					url: helper.webroot + "api/search/popular",
					method: 'GET',
					success: function(res) {
						that.popular = res.data.data;
					}
				})
			},
			/**
			 *	@author RustyLake	
			 *	@version 1.0.0
			 *	@ignore 2019-07-31
			 *	@method 清除历史记录;
			 * */
			cleanHistory: function() {
				let that = this;
				uni.showModal({
					title: '删除历史记录',
					content: '删除后不可恢复',
					success: function(res) {
						if (res.confirm) {
							uni.showLoading({
								title: '删除历史记录'
							})
		
							uni.removeStorage({
								key: "history",
								success: function(res) {
									uni.hideLoading();
									console.log('删除成功');
									console.log(res);
									that.history = null;
								},
								fail: function(res) {
									console.log('删除失败');
									console.log(res);
								}
							})
		
		
						} else {
							uni.showToast({
								title: '用户点击了取消按钮'
							})
						}
		
					}
				})
			},
			/**
			 * @author	RustyLake
			 * @version	1.0.0
			 * @ignore	2019-07-31
			 * @method	搜索文章或商家,并在历史记录中新增一条数据,历史记录保存最多为10条;
			 * */
			search: function() {
				let that = this;
				if (this.searchText === '' || this.searchText.length <= 0 || this.searchText === undefined || this.searchText ===
					null) {
					console.log('空字符串,不计入历史记录');
					uni.navigateTo({
						url: '../../searchItemDetail/searchItemDetail?keywords=' + that.popular[0].keyword_name
					})
					return;
				} else {
		
					let that = this;
					uni.getStorage({
						key: 'history',
						success: function(res) {
							//有搜索记录,是否大于10?;
							if (res.data.length >= 10) {
								//删除数组最后一个元素;
								res.data.splice(res.data.length - 1, 1);
								//向数组首部添加元素;
								res.data.unshift(that.searchText);
								that.history = res.data;
							} else {
								res.data.unshift(that.searchText);
								that.history = res.data;
							}
		
							uni.setStorage({
								key: 'history',
								data: that.history,
								success: function() {
									uni.navigateTo({
										url: '../../searchItemDetail/searchItemDetail?keywords=' + that.searchText
									})
								}
							})
							//更新缓存;
						},
						fail: function(res) {
							//无搜索记录;
							that.history = [];
							that.history.unshift(that.searchText);
							uni.setStorage({
								key: 'history',
								data: that.history,
								success: function() {
									uni.navigateTo({
										url: '../../searchItemDetail/searchItemDetail?keywords=' + that.searchText
									})
								}
							})
						}
					})
				}
			},
			/**
			 * @author	RustyLake
			 * @ignore	2019-08-01
			 * @method	获取搜索历史记录
			 * */
			getHistory: function() {
				let that = this;
				uni.getStorage({
					key: 'history',
					success: function(res) {
						that.history = res.data;
					},
					fail: function(res) {
						console.log('获取失败:');
						console.log(res);
					}
				})
			},
			getInputText: function(obj) {
				this.searchText = obj.target.value;
			},
			resetInputText: function() {
				uni.switchTab({
					url: '../index'
				})
			},
			toSearchDetail: function() {
				uni.navigateTo({
					url: '../../searchItemDetail/searchItemDetail'
				})
			}
		
	}
}
